'use client';
import { Metadata } from 'next';
import { AuthProvider } from '@/contexts/AuthenticationContext';
import { Noto_Sans_JP } from 'next/font/google';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { ReactQueryDevtools } from '@tanstack/react-query-devtools';

const notoSansCJKJP = Noto_Sans_JP({
  subsets: ['latin', 'cyrillic'],
  weight: ['100', '300', '400', '500', '700'],
});

const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      staleTime: 5000,
      gcTime: 1000 * 60 * 5,
      refetchOnWindowFocus: false,
    },
  },
});

export const metadata: Metadata = {
  title: {
    default: 'Your App Name', // Default title
    template: '%s | Your App Name', // Template for dynamic page titles
  },
  description: 'A brief description of your application',
  applicationName: 'Your App Name',
  authors: [{ name: 'Your Name', url: 'https://yourwebsite.com' }],
  generator: 'Next.js',
  keywords: ['your', 'app', 'keywords'],
  referrer: 'origin',
  themeColor: '#ffffff', // Customize this to match your app's color scheme
  colorScheme: 'light', // or 'dark'
  viewport: {
    width: 'device-width',
    initialScale: 1,
    maximumScale: 1,
  },
  robots: {
    index: true,
    follow: true,
    nocache: false,
    googleBot: {
      index: true,
      follow: true,
      noimageindex: false,
      'max-video-preview': -1,
      'max-image-preview': 'large',
      'max-snippet': -1,
    },
  },
  openGraph: {
    type: 'website',
    locale: 'en_US',
    url: 'https://yourwebsite.com',
    title: 'Your App Name',
    description: 'A brief description of your application',
    siteName: 'Your App Name',
    images: [
      {
        url: '/path/to/your/og-image.jpg',
        width: 1200,
        height: 630,
        alt: 'Your App Open Graph Image',
      },
    ],
  },
  twitter: {
    card: 'summary_large_image',
    title: 'Your App Name',
    description: 'A brief description of your application',
    images: ['/path/to/your/twitter-image.jpg'],
  },
  icons: {
    icon: [
      { url: '/favicon.ico' },
      { url: '/favicon-16x16.png', sizes: '16x16', type: 'image/png' },
      { url: '/favicon-32x32.png', sizes: '32x32', type: 'image/png' },
    ],
    apple: [
      { url: '/apple-touch-icon.png' },
    ],
  },
  manifest: '/manifest.json',
};

export default function RootLayout({ children }: { children: React.ReactNode }) {
  return (
    <QueryClientProvider client={queryClient}>
      <AuthProvider>
        <html lang="en" className={notoSansCJKJP.className}>
          <body className="min-h-screen flex flex-col">
            <main className="flex-1">{children}</main>
            <ReactQueryDevtools initialIsOpen={false} />
          </body>
        </html>
      </AuthProvider>
    </QueryClientProvider>
  );
}